body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

form label {
  display: block;
  font-weight: bold;
}

label {
  display: block;
  font-weight: bold;
}

.fruit-basket {
  background-image: url(/react-redux-navbar/static/media/Fruit_Basket.70f34342.png);
  background-repeat: no-repeat;
  background-position: center;
  height: 550px;
}

table th {
  text-align: left;
}
.banner {
	background-color: rgb(0, 0, 0);
	background-image: -webkit-linear-gradient(top, rgb(0, 0, 0),rgb(0, 0, 0),rgb(0, 0, 0),rgb(0, 0, 0),rgb(5, 5, 5),rgb(5, 5, 5),rgb(15, 15, 15));
	background-image: linear-gradient(180deg, rgb(0, 0, 0),rgb(0, 0, 0),rgb(0, 0, 0),rgb(0, 0, 0),rgb(5, 5, 5),rgb(5, 5, 5),rgb(15, 15, 15));
	height: 100vh;
	width: 100vw;
	max-width: 100%;
	overflow: hidden;
  }
  
  .color-container {
	display: table;
	margin-top: 20px;
	background-color: black;
	padding: 10px;
  }
  
  .colorpack {
	/*outline: 1px solid #ccc; */
	padding: 5px; 
	margin: 5px; 
	border-radius: 10%;
	background-color: white;
  }
  
  .color-image {
	height: 120px;
	display: table-cell;
	vertical-align: middle;
  }
  
  .colorpack-1:hover {
	overflow: visible;
	  box-shadow:
		0px 0px 30px white,
		0px 0px 30px white,
		0px 0px 30px white,
		0px 0px 30px white,
		0px 0px 55px blue,
		0px 0px 75px blue,
		0px 0px 95px blue
		  !important;
	  -webkit-transform: scale(1.1,1.1) translateY(-5px);
	          transform: scale(1.1,1.1) translateY(-5px);
	  -webkit-transition: box-shadow .5s ease scale(2) !important;
	  transition: box-shadow .5s ease scale(2) !important;
	  -webkit-transition-duration: .5s;
	          transition-duration: .5s;
  }
  
